import { newE2EPage } from '@stencil/core/testing';

describe('presto-dynamic-overlays', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-dynamic-overlays></presto-dynamic-overlays>');

    const element = await page.find('presto-dynamic-overlays');
    expect(element).toHaveClass('hydrated');
  });
});
